<template>
  <div class="main-container" @keydown="keydown" :style="{ background: bgList[bgVal] }">
    <div class="stars">
      <div class="star" v-for="(v, i) in starsCount" :key="i" ref="star"></div>
    </div>
    <div class="main-content">
      <span class="mian-text" :text="text"
        :style="{ color: textList[bgVal].color, '--maskColor': textList[bgVal].maskColor }">{{text}}</span>
      <div class="mian-input" :style="{ borderColor: bgColor[bgVal] }"><span class="mian-password"
          :style="{ color: bgColor[bgVal] }">{{ inputValue[0] }}</span></div>
      <div class="mian-input" :style="{ borderColor: bgColor[bgVal] }"><span class="mian-password"
          :style="{ color: bgColor[bgVal] }">{{ inputValue[1] }}</span></div>
      <div class="mian-input" :style="{ borderColor: bgColor[bgVal] }"><span class="mian-password"
          :style="{ color: bgColor[bgVal] }">{{ inputValue[2] }}</span></div>
      <div class="mian-input" :style="{ borderColor: bgColor[bgVal] }"><span class="mian-password"
          :style="{ color: bgColor[bgVal] }">{{ inputValue[3] }}</span></div>
    </div>
  </div>
</template>

<script>
import { md5 } from '@/utils'
import { mapState, mapMutations } from 'vuex'
export default {
  name: "Homestar",
  data() {
    return {
      inputValue: [],
      starsCount: 800, //星星数
      distance: 800, //星星间距
      bgVal: 1,
      timer: null,
      text:"何时答应我的",
      errorCount:0,
      bgList: [
        'radial-gradient(220% 105% at top center,#1b2947 10%,#77517d 40%,#e96f92 65%,#f7f7b6)',
        'radial-gradient(220% 105% at top center,#022E6A 10%,#7B68EE 40%,#87CEEB 65%,#87CEFA)',
      ],
      textList: [
        {
          color: '#BA55D3',
          maskColor: '#e96f92',
        },
        {
          color: '#40E0D0',
          maskColor: '#6495ED',
        }
      ],
      bgColor: [
        '#FFC0CB',
        '#87CEEB'
      ]
    };
  },
  mounted() {
    this.init();
    let _this = this
    document.onkeydown = function (e) {
      _this.keydown(e)
      if (!_this.musicOpen) {
        _this.changeMusic(true)
        _this.playRef.play()
      }
    }
    this.timer = setInterval(() => {
      if (this.bgVal < this.bgList.length - 1) {
        this.bgVal++
      } else {
        this.bgVal = 0
      }
    }, 5000)
  },
  unmounted() {
    clearInterval(this.timer)
  },
  watch: {
    inputValue: function (val) {
      if (val.length == 4) {
        this.submit()
      }
    }
  },
  computed: mapState({
    playRef: state => state.playRef,
    musicOpen: state => state.musicOpen
  }),
  unmounted() {
    document.onkeydown = function () { }
  },
  methods: {
    ...mapMutations([
      'changeMusic',
    ]),
    init() {
      let starArr = this.$refs.star;
      starArr.forEach((item) => {
        let speed = 0.2 + Math.random() * 1;
        let thisDistance = this.distance + Math.random() * 300;
        item.style.transformOrigin = `0 0 ${thisDistance}px`;
        item.style.transform = ` translate3d(0,0,-${thisDistance}px) rotateY(${Math.random() * 360
          }deg) rotateX(${Math.random() * -50}deg) scale(${speed},${speed})`;
      });
    },
    submit() {
      let password = this.inputValue.join('')
      if (md5(password) == '3810b20210601cb47aae3f1380a264d2') {
        localStorage.setItem('fytoken', 111)
        this.$router.push('/home')
      } else {
        if(this.errorCount<4){
          this.errorCount++
        }
        switch (this.errorCount) {
          case 1:
            this.text="哼，这时间都能记错"
            break;
          case 2:
            this.text="事不过三哈，你看着办"
            break;
          case 3:
            this.text="臭猪不晓得问吗！！！"
            break;
          case 4:
            this.text="气死我了！！！！！！"
            break;
          default:
            break;
        }
        console.log('验证失败')
      }
    },
    keydown(e) {
      console.log(e.key, 'e.key')
      if (e.key == "Backspace" && this.inputValue.length > 0) {
        this.inputValue.pop()
        console.log(this.inputValue, 'delete')

      }
      if (/\d/.test(Number(e.key))) {
        if (this.inputValue.length < 4) {
          this.inputValue.push(e.key)
        }
        console.log(this.inputValue, 'inputValue')
      }
    }
  },
};
</script>

<style scoped>
.first-container {
  width: 100%;
  height: 100vh;
  background: #fff;
  z-index: 90;
}

.main-container {
  overflow: hidden;
  /* background: radial-gradient(200% 100% at bottom center,#f7f7b6,#e96f92,#1b2947); */
  /* background: radial-gradient(220% 105% at top center, #1b2947 10%, #77517d 40%, #e96f92 65%, #f7f7b6); */
  /* #87CEFA #007FFF*/
  /* 1.#191970 5%  #77517d 40%  #87CEFA 65% #E1FFFF*/
  /* 2.#022E6A 10%,#6495ED 40%,#87CEEB 65%,#48D1CC */
  /* background: radial-gradient(220% 105% at top center,#022E6A 10%,#6495ED 40%,#87CEEB 65%,#48D1CC); */
  background-attachment: fixed;
  width: 100%;
  height: 100vh;
}

/* 动画  */
@keyframes rotate {
  0% {
    transform: perspective(400px) rotateX(-40deg) rotateY(0deg) rotateZ(20deg);
  }

  100% {
    transform: perspective(400px) rotateX(-40deg) rotateY(-360deg) rotateZ(20deg);
  }
}

.stars {
  transform: perspective(500px);
  transform-style: preserve-3d;
  position: absolute;
  perspective-origin: 50% 100%;
  left: 50%;
  animation: rotate 90s infinite linear;
  bottom: 0;
  max-width: 100vw;
  max-height: 100vh;
}

.star {
  width: 2px;
  height: 2px;
  border-radius: 50%;
  background: #f7f7b8;
  position: absolute;
  top: 0;
  left: 0;
  backface-visibility: hidden;
}

.main-content {
  width: 500px;
  height: 80px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
}

.mian-text {
  position: absolute;
  white-space: nowrap;
  top: -80px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 40px;
  color: #BA55D3;
  /* background: linear-gradient(to right,#BA55D3,#e96f92);
  -webkit-background-clip: text; */
}

.mian-text::before {
  content: attr(text);
  position: absolute;
  z-index: 10;
  color: var(--maskColor);
  -webkit-mask: linear-gradient(to right, transparent, #fff);
}

.mian-input {
  width: 80px;
  height: 80px;
  border: 2px solid #87CEEB;
  background: #fff;
  text-align: center;
}

.mian-password {
  line-height: 80px;
  font-size: 50px;
  font-weight: bold;
  color: #48D1CC;

}
</style>



