import Vue from "vue";
import Vuex from "vuex";
// import axios from 'axios'

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        musicOpen: false,
        playRef: null,
    },
    getters: {},
    mutations: {
        changeMusic(state, payload) {
            state.musicOpen = payload;
        },
        initPlayer(state, payload) {
            console.log("payload", payload);
            state.playRef = payload;
        },
    },
    actions: {
    },
    modules: {},
});
