<template>
  <div id="app" @click="play">
    <!-- 需要加个初始页面 点击播放歌曲 -->
    <router-view></router-view>
    <player />
  </div>
</template>

<script>
import player from './components/player.vue';
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'App',
  components: {
    player
  },
  computed: mapState({
    playRef: state => state.playRef,
    musicOpen: state => state.musicOpen
  }),
  methods: {
    ...mapMutations([
      'changeMusic'
    ]),
    play() {
      if (!this.musicOpen) {
        this.changeMusic(true)
        this.playRef.play()
      }
    },
  }
}
</script>

