<style scoped>
.container {
  width: 100%;
  height: 100vh;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper_slide {
  height: 100%;
  /* background: url('~@/assets/icon/love.png'); */
  /* background-repeat: repeat; */
}

.swiper-prev {
