<template>
    <div style='width:100%,height:100%,position: relative;'>
        <div id="container"></div>
        <div id="menu">
            <button id="sphere" @click="check('sphere')">I</button>
            <button id="helix" @click="check('helix')">♥</button>
            <button id="grid" @click="check('grid')">U</button>
        </div>
        <div class="title-3d" text="♥以后我们的照片要填满整个星空♥">♥以后我们的照片要填满整个星空♥</div>
    </div>

</template>

<script>
import * as THREE from "three";
import { TWEEN } from "three/examples/jsm/libs/tween.module.min.js";
import { TrackballControls } from "three/examples/jsm/controls/TrackballControls";
import { CSS3DRenderer, CSS3DObject } from "three/examples/jsm/renderers/CSS3DRenderer";
const imgList = [
    require('@/assets/img/aini.jpg'),
    require('@/assets/img/geli2.jpg'),
    require('@/assets/img/bianzi2.jpg'),
    require('@/assets/img/chelizi.jpg'),
    require('@/assets/img/geli.jpg'),
    require('@/assets/img/shipin.png'),
    require('@/assets/img/aini2.jpg'),
    require('@/assets/img/manhua5.jpg'),
    require('@/assets/img/geli5.jpg'),
    require('@/assets/img/bianzi.jpg'),
    require('@/assets/img/manhua2.jpg'),
    require('@/assets/img/liaotian2.jpg'),
    require('@/assets/img/geli6.jpg'),
    require('@/assets/img/yiqian4.jpg'),
    require('@/assets/img/mianmo.jpg'),
    require('@/assets/img/guaiqiao.jpg'),
    require('@/assets/img/geli4.jpg'),
    require('@/assets/img/hehao.jpg'),
    require('@/assets/img/liaotian1.jpg'),
    require('@/assets/img/manhua1.jpg'),
    require('@/assets/img/huahua.jpg'),
    require('@/assets/img/geli3.jpg'),
    require('@/assets/img/huoyin.jpg'),
    require('@/assets/img/keai.jpg'),
    require('@/assets/img/kuang.jpg'),
    require('@/assets/img/yiqian3.jpg'),
    require('@/assets/img/kuang2.jpg'),
    require('@/assets/img/liaotian.jpg'),
    require('@/assets/img/manhua4.jpg'),
    require('@/assets/img/yiqian2.jpg'),
    require('@/assets/img/riluo.jpg'),
    require('@/assets/img/manhua6.jpg'),
    require('@/assets/img/yiqian5.jpg'),
    require('@/assets/img/riluo2.jpg'),
    require('@/assets/img/wan.jpg'),
    require('@/assets/img/xiaoshihou.jpg'),
    require('@/assets/img/yanjing.jpg'),
    require('@/assets/img/liaotian3.jpg'),
    require('@/assets/img/ditie.jpg'),
    require('@/assets/img/manhua3.jpg'),
    require('@/assets/img/yiqian1.jpg'),
]
export default {
    data() {
        return {
            camera: null,
            imgList,
            scene: null,
            renderer: null,
            controls: null,
            objects: [],
            targets: { sphere: [], helix: [], grid: [] },
        };
    },
    mounted() {
        this.init();
        this.animate();
        console.log('pic mounted')
    },
    methods: {
        init() {
            this.camera = new THREE.PerspectiveCamera(40, window.innerWidth / window.innerHeight, 1, 10000);
            this.camera.position.z = 3500;
            this.camera.position.x = -500;
            this.scene = new THREE.Scene();
            //样式
            for (let i = 0; i < this.imgList.length; i++) {
                const element = document.createElement("div");
                element.className = "element";
                element.style.backgroundColor = "rgba(0,127,127," + (Math.random() * 0.5 + 0.25) + ")";

                const img = document.createElement("img");
                img.className = "img";
                img.src = this.imgList[i]
                element.appendChild(img);

                // const symbol = document.createElement("div");
                // symbol.className = "symbol";
                // symbol.textContent = this.imgList[i];
                // element.appendChild(symbol);

                // const details = document.createElement("div");
                // details.className = "details";
                // details.innerHTML = this.imgList[i + 1] + "<br>" + this.imgList[i + 2];
                // element.appendChild(details);

                const objectCSS = new CSS3DObject(element);
                objectCSS.position.x = Math.random() * 4000 - 2000;
                objectCSS.position.y = Math.random() * 4000 - 2000;
                objectCSS.position.z = Math.random() * 4000 - 2000;
                this.scene.add(objectCSS);

                this.objects.push(objectCSS);
            }

            // sphere

            const vector = new THREE.Vector3();

            for (let i = 0, l = this.objects.length; i < l; i++) {
                const phi = Math.acos(-1 + (2 * i) / l);
                const theta = Math.sqrt(l * Math.PI) * phi;

                const object = new THREE.Object3D();

                object.position.setFromSphericalCoords(1000, phi, theta);

                vector.copy(object.position).multiplyScalar(2);

                object.lookAt(vector);

                this.targets.sphere.push(object);
            }

            // helix

            for (let i = 0, l = this.objects.length; i < l; i++) {
                const theta = i * 1 + Math.PI;
                const y = -(i * 30) + 1000;

                const object = new THREE.Object3D();

                object.position.setFromCylindricalCoords(800, theta, y);

                vector.x = object.position.x * 3;
                vector.y = object.position.y;
                vector.z = object.position.z * 3;

                object.lookAt(vector);

                this.targets.helix.push(object);
            }

            // grid

            for (let i = 0; i < this.objects.length; i++) {
                const object = new THREE.Object3D();

                object.position.x = (i % 3) * 400 - 800;
                object.position.y = -(Math.floor(i / 3) % 3) * 400 + 800;
                object.position.z = Math.floor(i / 9) * 500 - 1000;

                this.targets.grid.push(object);
            }

            //

            this.renderer = new CSS3DRenderer();
            this.renderer.setSize(window.innerWidth, window.innerHeight);
            document.getElementById("container").appendChild(this.renderer.domElement);

            //

            this.controls = new TrackballControls(this.camera, this.renderer.domElement);
            this.controls.minDistance = 500;
            this.controls.maxDistance = 6000;
            this.controls.rotateSpeed = 10.0
            this.controls.addEventListener("change", this.render);

            // const buttonSphere = document.getElementById("sphere");
            // buttonSphere.addEventListener("click", function () {
            //     this.transform(this.targets.sphere, 2000);
            // });

            // const buttonHelix = document.getElementById("helix");
            // buttonHelix.addEventListener("click", function () {
            //     this.transform(this.targets.helix, 2000);
            // });

            // const buttonGrid = document.getElementById("grid");
            // buttonGrid.addEventListener("click", function () {
            //     this.transform(this.targets.grid, 2000);
            // });

            this.transform(this.targets.grid, 4000);

            window.addEventListener("resize", this.onWindowResize);
        },
        check(value) {
            this.transform(this.targets[value], 2000);
        },
        transform(targets, duration) {
            TWEEN.removeAll();

            for (let i = 0; i < this.objects.length; i++) {
                const object = this.objects[i];
                const target = targets[i];

                new TWEEN.Tween(object.position)
                    .to(
                        {
                            x: target.position.x,
                            y: target.position.y,
                            z: target.position.z,
                        },
                        Math.random() * duration + duration
                    )
                    .easing(TWEEN.Easing.Exponential.InOut)
                    .start();

                new TWEEN.Tween(object.rotation)
                    .to(
                        {
                            x: target.rotation.x,
                            y: target.rotation.y,
                            z: target.rotation.z,
                        },
                        Math.random() * duration + duration
                    )
                    .easing(TWEEN.Easing.Exponential.InOut)
                    .start();
            }

            new TWEEN.Tween(this)
                .to({}, duration * 2)
                .onUpdate(this.render)
                .start();
        },

        onWindowResize() {
            this.camera.aspect = window.innerWidth / window.innerHeight;
            this.camera.updateProjectionMatrix();

            this.renderer.setSize(window.innerWidth, window.innerHeight);

            this.render();
        },

        animate() {
            requestAnimationFrame(this.animate);

            TWEEN.update();

            this.controls.update();
        },

        render() {
            this.renderer.render(this.scene, this.camera);
        },
    },
};
</script>

<style>
a {
    color: #8ff;
}

.title-3d {
    position: absolute;
    z-index: 1;
    left: 50%;
    transform: translateX(-50%);
    top:60px;
    font-size: 30px;
  color: #00CED1;

}
.title-3d::before {
    content: attr(text);
  position: absolute;
  z-index: 10;
  color: #9400D3;
  -webkit-mask: linear-gradient(to right, transparent, #fff);
}
#menu {
    position: absolute;
    bottom: 20px;
    width: 100%;
    text-align: center;
}

.element {
    width: 200px;
    /* height: 160px; */
    box-shadow: 0px 0px 12px rgba(0, 255, 255, 0.5);
    border: 1px solid rgba(127, 255, 255, 0.25);
    font-family: Helvetica, sans-serif;
    text-align: center;
    line-height: normal;
    cursor: default;
}

.element:hover {
    box-shadow: 0px 0px 12px rgba(0, 255, 255, 0.75);
    border: 1px solid rgba(127, 255, 255, 0.75);
}

.element .img {
    position: relative;
    width: 100%;
}

.element .symbol {
    position: absolute;
    top: 40px;
    left: 0px;
    right: 0px;
    font-size: 60px;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.75);
    text-shadow: 0 0 10px rgba(0, 255, 255, 0.95);
}

.element .details {
    position: absolute;
    bottom: 15px;
    left: 0px;
    right: 0px;
    font-size: 12px;
    color: rgba(127, 255, 255, 0.75);
}

button {
    color: rgba(127, 255, 255, 0.75);
    background: transparent;
    outline: 1px solid rgba(127, 255, 255, 0.75);
    border: 0px;
    padding: 5px 10px;
    cursor: pointer;
}

button:hover {
    background-color: rgba(0, 255, 255, 0.5);
}

button:active {
    color: #000000;
    background-color: rgba(0, 255, 255, 0.75);
}
</style>
