<template>
    <div class="player-container">
        <img class="cr" :class="musicOpen ? 'iconRotate' : ''"
            :src="musicOpen ? require('@/assets/icon/zhuan.png') : require('@/assets/icon/ting.png')" alt=""
            @click.stop="control" id="img">
        <audio ref="player" autoplay :src="list[index]">
        </audio>
    </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {

    data() {
        return {
            index: 0,
            list: [require('@/assets/media/suisui.mp3'), require('@/assets/media/huida.mp3'), require('@/assets/media/spotlight.mp3')],
        }
    },
    mounted() {
        this.initPlayer(this.$refs.player)//设为全局播放器
        this.$refs.player.addEventListener("ended", () => {
            if (this.index == this.list.length - 1) {
                this.index = 0
            } else {
                this.index++
            }
            this.$nextTick(() => {
                this.$refs.player.play()
            })
        })
    },
    computed: mapState({
        musicOpen: state => state.musicOpen,
    }),
    methods: {
        ...mapMutations([
            'changeMusic','initPlayer'
        ]),
        control: function () {
            if (this.musicOpen) {
                this.changeMusic(false)
                this.$refs.player.pause()
            } else {
                this.changeMusic(true)
                this.$refs.player.play()
            }
        },
    }

}
</script>

<style scoped>
.player-container {
    position: fixed;
    right: 50px;
    top: 50px;
    z-index: 90;
}

.cr {
    width: 40px;
    height: 40px;
}

.iconRotate {
    animation: spin 2s linear infinite;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
        -ms-transform: rotate(0deg);
        /* IE 9 */
        -moz-transform: rotate(0deg);
        /* Firefox */
        -webkit-transform: rotate(0deg);
        /* Safari 和 Chrome */
        -o-transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
        -ms-transform: rotate(360deg);
        /* IE 9 */
        -moz-transform: rotate(360deg);
        /* Firefox */
        -webkit-transform: rotate(360deg);
        /* Safari 和 Chrome */
        -o-transform: rotate(360deg);
    }
}

/* .player{
        visibility: hidden;
        width: 0;
        height: 0;
    } */
</style>